* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "PingFang TC", "Microsoft JhengHei", sans-serif;
  font-weight: bold; }

html, body {
  width: 100%;
  height: 100%; }

ul, li {
  list-style: none; }

a {
  text-decoration: none;
  color: black; }

button:hover {
  cursor: pointer; }

input[type="submit"]:hover {
  cursor: pointer; }

.p-index {
  background-attachment: fixed;
  background-image: url("../img/bg-default.jpg"); }
  .p-index .container {
    background-color: rgba(0, 0, 0, 0.6);
    min-height: 100vh; }
    .p-index .container .head {
      text-align: center; }
      .p-index .container .head > h1 {
        color: white;
        padding-top: 20px;
        padding-bottom: 10px;
        margin: 0; }
      .p-index .container .head > a {
        color: white; }
        .p-index .container .head > a:hover {
          color: #1000ff; }
  .p-index .row {
    margin: 0 auto;
    padding-top: 30px;
    width: 940px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    .p-index .row .col {
      width: 300px;
      color: #EAF0ED;
      line-height: 1.5rem;
      margin-bottom: 20px;
      transition: filter 1s, box-shadow 1s 0.5s; }
      .p-index .row .col .preview {
        position: relative;
        width: 100%;
        height: 168.75px; }
        .p-index .row .col .preview > img {
          position: absolute;
          width: 100%;
          height: 100%;
          opacity: 0; }
        .p-index .row .col .preview:before {
          position: absolute;
          width: 100%;
          height: 100%;
          content: '';
          background-image: url("data:image/jpeg;base64,"); }
      .p-index .row .col .descript {
        background-color: rgba(0, 0, 0, 0.4); }
        .p-index .row .col .descript:before {
          content: "";
          display: table; }
        .p-index .row .col .descript:after {
          content: "";
          display: table;
          clear: both; }
        .p-index .row .col .descript .avatar {
          position: relative;
          margin: 5px;
          width: 50px;
          height: 50px;
          display: block;
          float: left; }
          .p-index .row .col .descript .avatar > img {
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            opacity: 0; }
          .p-index .row .col .descript .avatar:before {
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            content: '';
            background-image: url("data:image/jpeg;base64,"); }
      .p-index .row .col .descript .introduce {
        float: left;
        margin-top: 7px;
        white-space: nowrap;
        width: 230px; }
        .p-index .row .col .descript .introduce .ch-name {
          overflow: hidden;
          text-overflow: ellipsis; }
      .p-index .row .col:hover {
        cursor: pointer;
        filter: brightness(1.4);
        box-shadow: 5px 5px 10px 5px #333; }
