.p-index {
	background-attachment: fixed;
	background-image: url("../img/bg-default.jpg");
	.container {
		background-color: rgba(0,0,0,0.6);
		min-height: 100vh;
		.head {
			text-align: center;
			> h1 {
				color: white;
				padding-top: 20px;
				padding-bottom: 10px;
				margin: 0; }
			> a {
				color: white;
				&:hover {
					color: #1000ff; } } } }
	.row {
		margin: 0 auto;
		padding-top: 30px;
		width: 940px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		.col {
			width: 300px;
			color: $c-secondary;
			line-height: 1.5rem;
			margin-bottom: 20px;
			transition: filter 1s, box-shadow 1s 0.5s;
			.preview {
				position: relative;
				width: 100%;
				height: 168.75px;
				> img {
					position: absolute;
					width: 100%;
					height: 100%;
					opacity: 0; }
				&:before {
					position: absolute;
					width: 100%;
					height: 100%;
					content: '';
					// background-image: url('../img/404_preview-320x180.jpg')
					background-image: url('data:image/jpeg;base64,'); } }
			.descript {
				@include clearfix();
				background-color: rgba(0,0,0, 0.4);
				.avatar {
					position: relative;
					margin: 5px;
					width: 50px;
					height: 50px;
					display: block;
					float: left;
					> img {
						position: absolute;
						width: 100%;
						height: 100%;
						border-radius: 50%;
						opacity: 0; }
					&:before {
						position: absolute;
						width: 100%;
						height: 100%;
						border-radius: 50%;
						content: '';
						// background-image: url('../img/404_preview-320x180.jpg')
						background-image: url('data:image/jpeg;base64,'); } } }
			.descript {
				.introduce {
					float: left;
					margin-top: 7px;
					white-space: nowrap;
					width: 230px;
					.ch-name {
						overflow: hidden;
						text-overflow: ellipsis; } } }
			&:hover {
				cursor: pointer;
				filter: brightness(1.4);
				box-shadow: 5px 5px 10px 5px #333; } } } }







